@charset "UTF-8";

html, body, p, h1, h2, h3, h4, ul, article, figure, ol {
  padding: 0;
  margin: 0;
  font-weight: normal; }

body {
  line-height: 1.2;
  font-family: os;
  font-size: 14px; }

a {
  color: inherit;
  text-decoration: none; }

nav a, li a, .btn {
  text-decoration: none;
  -webkit-transition: all 0.15s ease-in-out;
  transition: all 0.15s ease-in-out;
  cursor: pointer;
  outline: none; }

button {
  text-decoration: none;
  -webkit-transition: all 0.15s ease-in-out;
  transition: all 0.15s ease-in-out;
  cursor: pointer;
  outline: none;
  &:before, &:after {
    text-decoration: none;
    -webkit-transition: all 0.15s ease-in-out;
    transition: all 0.15s ease-in-out;
    cursor: pointer;
    outline: none; } }

img {
  max-width: 100%;
  max-height: 100%; }

ul, ol {
  list-style: none; }

.btn, input, textarea, button {
  outline: none; }

.row,.row-blk {
  position: relative;
  width: 1160px;
  margin: 0 auto;
  height: 100%;
  &:after {
    display: block;
    content: '';
    clear: both; } }

