@font-face {
  font-family: oseb;
  src: url("/fonts/OpenSans-ExtraBold.ttf"); }


@font-face {
  font-family: osb;
  src: url("/fonts/OpenSans-Bold.ttf"); }


input, textarea, button {
  outline: none;
  font-size: 14px;
  font-family: os; }

 ::-webkit-input-placeholder, ::-moz-placeholder, :-moz-placeholder, :-ms-input-placeholder {
  color: $color-black; }

